___CSS_LOADER_EXPORT___.push([module.id, `.sxa-search-box {
  position: relative;
}
.sxa-search-box .search-form {
  position: relative;
}
.sxa-search-box .search-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}
.sxa-search-box .search-input {
  flex-grow: 1;
  padding: 12px 50px 12px 16px;
  background: var(--bg-body);
  border: 1px solid var(--border-color);
  border-radius: 0.375rem;
  font-size: 15px;
  color: var(--text-primary);
  transition: all 0.15s ease-in-out;
}
.sxa-search-box .search-input:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(0, 120, 212, 0.1);
}
.sxa-search-box .search-submit {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  padding: 8px;
  background: transparent;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  border-radius: 0.25rem;
}
.sxa-search-box .search-submit:hover {
  color: var(--color-primary);
  background: var(--bg-surface);
}
.sxa-search-box .search-suggestions {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  right: 0;
  background: var(--bg-elevated);
  border: 1px solid var(--border-color);
  border-radius: 0.375rem;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
  z-index: 1000;
}
.sxa-search-box .search-suggestions[hidden] {
  display: none;
}
.sxa-search-box .suggestions-list {
  margin: 0;
  padding: 8px 0;
  list-style: none;
  max-height: 300px;
  overflow-y: auto;
}
.sxa-search-box .suggestion-item a {
  display: block;
  padding: 10px 16px;
  color: var(--text-primary);
  text-decoration: none;
  transition: all 0.15s ease-in-out;
}
.sxa-search-box .suggestion-item a:hover {
  background: var(--bg-surface);
  color: var(--color-primary);
